:root {
  // material design tokens
  // light
  --light-primary: rgb(0 37 57);
  --light-surface-tint: rgb(0 100 146);
  --light-on-primary: rgb(255 255 255);
  --light-primary-container: rgb(0 71 105);
  --light-on-primary-container: rgb(255 255 255);
  --light-secondary: rgb(0 37 57);
  --light-on-secondary: rgb(255 255 255);
  --light-secondary-container: rgb(29 70 97);
  --light-on-secondary-container: rgb(255 255 255);
  --light-tertiary: rgb(0 41 9);
  --light-on-tertiary: rgb(255 255 255);
  --light-tertiary-container: rgb(0 78 25);
  --light-on-tertiary-container: rgb(255 255 255);
  --light-error: rgb(78 0 2);
  --light-on-error: rgb(255 255 255);
  --light-error-container: rgb(140 0 9);
  --light-on-error-container: rgb(255 255 255);
  --light-background: rgb(246 250 255);
  --light-on-background: rgb(23 28 33);
  --light-surface: rgb(246 250 255);
  --light-on-surface: rgb(0 0 0);
  --light-surface-variant: rgb(218 228 238);
  --light-on-surface-variant: rgb(27 37 45);
  --light-outline: rgb(58 68 76);
  --light-outline-variant: rgb(203, 209, 215);
  --light-shadow: rgb(0 0 0);
  --light-scrim: rgb(0 0 0);
  --light-inverse-surface: rgb(44 49 54);
  --light-inverse-on-surface: rgb(255 255 255);
  --light-inverse-primary: rgb(221 238 255);
  --light-primary-fixed: rgb(0 71 105);
  --light-on-primary-fixed: rgb(255 255 255);
  --light-primary-fixed-dim: rgb(0 48 73);
  --light-on-primary-fixed-variant: rgb(255 255 255);
  --light-secondary-fixed: rgb(29 70 97);
  --light-on-secondary-fixed: rgb(255 255 255);
  --light-secondary-fixed-dim: rgb(0 48 73);
  --light-on-secondary-fixed-variant: rgb(255 255 255);
  --light-tertiary-fixed: rgb(0 78 25);
  --light-on-tertiary-fixed: rgb(255 255 255);
  --light-tertiary-fixed-dim: rgb(0 53 14);
  --light-on-tertiary-fixed-variant: rgb(255 255 255);
  --light-surface-dim: rgb(214 218 224);
  --light-surface-bright: rgb(246 250 255);
  --light-surface-container-lowest: rgb(255 255 255);
  --light-surface-container-low: rgb(250, 252, 255);
  --light-surface-container: rgb(234 238 244);
  --light-surface-container-high: rgb(228 232 239);
  --light-surface-container-highest: rgb(222 227 233);

  // dark
  --dark-primary: #95cdf7;
  --dark-surface-tint: rgb(139 206 255);
  --dark-on-primary: rgb(0 0 0);
  --dark-primary-container: rgb(149 209 255);
  --dark-on-primary-container: rgb(0 0 0);
  --dark-secondary: rgb(249 251 255);
  --dark-on-secondary: rgb(0 0 0);
  --dark-secondary-container: rgb(168 207 239);
  --dark-on-secondary-container: rgb(0 0 0);
  --dark-tertiary: rgb(240 255 235);
  --dark-on-tertiary: rgb(0 0 0);
  --dark-tertiary-container: rgb(117 225 129);
  --dark-on-tertiary-container: rgb(0 0 0);
  --dark-error: rgb(255 249 249);
  --dark-on-error: rgb(0 0 0);
  --dark-error-container: rgb(255 186 177);
  --dark-on-error-container: rgb(0 0 0);
  --dark-background: rgb(15 20 24);
  --dark-on-background: rgb(222 227 233);
  --dark-surface: rgb(15 20 24);
  --dark-on-surface: rgb(255 255 255);
  --dark-surface-variant: rgb(62 72 80);
  --dark-on-surface-variant: rgb(249 251 255);
  --dark-outline: rgb(194 204 214);
  --dark-outline-variant: rgb(194 204 214);
  --dark-shadow: rgb(0 0 0);
  --dark-scrim: rgb(0 0 0);
  --dark-inverse-surface: rgb(222 227 233);
  --dark-inverse-on-surface: rgb(0 0 0);
  --dark-inverse-primary: rgb(0 45 69);
  --dark-primary-fixed: rgb(210 234 255);
  --dark-on-primary-fixed: rgb(0 0 0);
  --dark-primary-fixed-dim: rgb(149 209 255);
  --dark-on-primary-fixed-variant: rgb(0 24 39);
  --dark-secondary-fixed: rgb(210 234 255);
  --dark-on-secondary-fixed: rgb(0 0 0);
  --dark-secondary-fixed-dim: rgb(168 207 239);
  --dark-on-secondary-fixed-variant: rgb(0 24 39);
  --dark-tertiary-fixed: rgb(145 255 154);
  --dark-on-tertiary-fixed: rgb(0 0 0);
  --dark-tertiary-fixed-dim: rgb(117 225 129);
  --dark-on-tertiary-fixed-variant: rgb(0 27 5);
  --dark-surface-dim: rgb(15 20 24);
  --dark-surface-bright: rgb(53 58 63);
  --dark-surface-container-lowest: rgb(10 15 19);
  --dark-surface-container-low: rgb(23 28 33);
  --dark-surface-container: rgb(27 32 37);
  --dark-surface-container-high: rgb(37 43 47);
  --dark-surface-container-highest: rgb(48 53 58);

  --transition: all 0.3s ease-in-out;
  --green: green;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "DM Sans", sans-serif;
  scroll-behavior: smooth;
  color: var(--light-on-surface);
}

ul {
  list-style: none;
}
.row-container,
#main {
  padding-left: 24px;
  padding-right: 24px;
  width: 100%;
  max-width: 1520px;
  margin: 0 auto;
  @media (max-width: 600px) {
    padding-left: 16px;
    padding-right: 16px;
  }
}
.archive {
  #main {
    @media (max-width: 700px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
}
/*alignment*/

.left-align {
  text-align: left;
}

.center-align {
  text-align: center;
}

.right-align {
  text-align: right;
}
.italic {
  font-style: italic;
}
/*font size*/
.h1 {
  font-size: 4rem;
  margin: 0;
  line-height: 4.5rem;
  font-weight: 500;
  letter-spacing: 0.2rem;
  color: var(--light-on-surface);
  @media (max-width: 1020px) {
    font-size: 3rem;
    line-height: 3.3rem;
  }
  @media (max-width: 600px) {
    font-size: 2rem;
    line-height: 2.2rem;
  }
}
.h2 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.2rem;
  color: var(--light-on-surface);
}
.underline {
  border-bottom: 1px solid var(--light-outline-variant);
}
.h3 {
  font-size: 2rem;
  font-weight: 500;
  letter-spacing: 0.05rem;
  line-height: 3rem;
  color: var(--light-tertiary);
}
.h4 {
  font-size: 1.3rem;
  font-weight: 500;
  line-height: 1.8rem;
  letter-spacing: 0.05rem;
  color: var(--light-on-surface);
}
.h5 {
  font-size: 1.1rem;
  color: var(--light-on-surface);
}
.h6 {
  font-size: 1.6rem;
  color: var(--light-on-surface);
}
.subtitle1 {
  font-size: 1.2rem;
  line-height: 1.7rem;
  color: var(--light-on-surface);

  font-weight: 300;
  color: var(--light-on-surface);
}
// create body1 a variable

.body1 {
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--light-on-surface-variant);
  font-weight: 400;
}
.body2 {
  font-size: 0.8rem;
  line-height: 1.2rem;
  color: var(--light-on-surface-variant);
  font-weight: 400;
}
/* letter spacing */

.text-decoration-none {
  text-decoration: none;
}
/* new buttons */
.primary-button {
  display: inline-block;
  padding: 12px 32px 12px 32px;
  cursor: pointer;
  font-weight: 400;
  background: var(--light-primary);
  text-decoration: none;
  transition: var(--transition);
  border-radius: 50px;
  text-align: center;
  border: none;

  color: var(--light-on-primary);
  font-size: 1rem !important;
  &:hover {
    filter: brightness(0.8);
  }
  span {
    color: var(--light-on-primary-container);
    font-size: 1rem !important;
    text-align: center;
    display: block;
    width: 100%;
  }
  svg {
    path {
      fill: var(--light-on-primary-container) !important;
    }
    margin-right: 4px;
  }
}
.secondary-button {
  border-radius: 50px;

  padding: 16px 32px 16px 32px;
  font-weight: 400;
  cursor: pointer;
  border: 1px solid var(--light-primary);
  background: none;
  letter-spacing: 0.05rem;
  text-decoration: none;
  transition: var(--transition);
  display: inline-block;
  color: var(--light-primary);
  span {
    color: var(--light-primary) !important;
    transition: var(--transition);
    line-height: 0;
    font-size: 1.1rem;
  }
  svg {
    position: relative;
    top: 0;
    transition: var(--transition);

    path,
    circle,
    line {
      fill: var(--primary);
      transition: var(--transition);
    }
  }
  &:hover {
    background: var(--light-primary);
    color: var(--light-on-primary-container) !important;

    span {
      color: var(--light-on-primary-container) !important;
    }
    svg {
      path {
        fill: white;
      }
      circle {
        stroke: white !important;
      }
      line {
        stroke: white !important;
      }
    }
  }
}
.tertiary-outline-button {
  border-radius: 50px;

  padding: 16px 32px 16px 32px;
  font-weight: 400;
  cursor: pointer;
  border: 1px solid var(--light-tertiary);
  background: none;
  letter-spacing: 0.05rem;
  text-decoration: none;
  transition: var(--transition);
  display: inline-block;
  color: var(--light-tertiary);
  span {
    color: var(--light-tertiary) !important;
    transition: var(--transition);
    line-height: 0;
    font-size: 1.1rem;
  }
  svg {
    position: relative;
    top: 0;
    transition: var(--transition);

    path,
    circle,
    line {
      fill: var(--tertiary);
      transition: var(--transition);
    }
  }
  &:hover {
    background: var(--light-tertiary);
    color: var(--light-on-tertiary-container) !important;

    span {
      color: var(--light-on-tertiary-container) !important;
    }
    svg {
      path {
        fill: white;
      }
      circle {
        stroke: white !important;
      }
      line {
        stroke: white !important;
      }
    }
  }
}

.link-button {
  font-weight: 350;

  cursor: pointer;
  background: none;
  letter-spacing: 0.05rem;
  text-decoration: none;
  transition: var(--transition);
  display: flex;
  align-items: center;

  border: none;
  text-decoration: underline;
  span {
    color: var(--light-secondary);
    transition: var(--transition);
    line-height: 0;
    font-size: 1.1rem;
  }
  svg {
    position: relative;
    top: 0;
    transition: var(--transition);
  }
}
.border-button {
  display: inline-block;
  padding: 12px 32px 12px 32px;
  cursor: pointer;
  font-weight: 400;
  border: 1px solid var(--light-secondary);
  letter-spacing: 0.05rem;
  text-decoration: none;
  transition: var(--transition);
  border-radius: 50px;
  text-align: center;
  &:hover {
    filter: brightness(0.8);
    background: var(--light-secondary);
  }
  span {
    color: var(--light-secondary);
    font-size: 1.2rem !important;
  }
  &:hover span {
    color: white !important;
  }
  svg {
    path {
      fill: var(--light-secondary);
    }
    margin-right: 4px;
  }
}

/*shadow */

.box-shadow {
  box-shadow: 3px 3px 20px 1px rgba(0, 0, 0, 0.2);
}

/*breadcrumbs*/

#breadcrumbs {
  color: #666666;
  text-decoration: none;
  font-size: 14px;
  padding-bottom: 10px;
}

#breadcrumbs a {
  color: #666666;
  text-decoration: none;
  font-size: 14px;
  letter-spacing: 0.02em;
  padding-bottom: 10px;
}

.visible {
  opacity: 1;
  visibility: visible;
}

.invisible {
  opacity: 0;
  visibility: hidden;
}

/*overlay*/
.dark-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: 100 !important;
  background: rgba(0, 0, 0, 0.4);
  display: none;
}

.white-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.4);
  display: none;
  i {
    position: fixed;
    font-size: 1.3rem;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
.foreground-loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.8);
  display: none;
  .loading-icon {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    div {
      width: 35px;
      height: 35px;
      border: 3px solid var(--darkGrey);
      border-color: var(--darkGrey) transparent transparent transparent;
    }
  }
}

.invisible {
  display: none;
}

/*save button*/

