    display: none !important;
  }
}
/*bread crumb*/

#breadcrumbs a:hover {
  text-decoration: underline;
}

/* lightbox for design board */

/* close button */
.close-button {
  display: block;
  width: 100%;
  background: none;
  color: var(--darkGrey);
  border: 1px solid var(--darkGrey);
  margin: 10px 0;
  padding: 10px 0;
  font-size: 1rem;
  text-transform: uppercase;
}

/* or element */
.or-element {
  width: 100%;
}
// button disabled
button:disabled,
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

/*font weight*/

.black {
  font-weight: 900;
}

.bold {
  font-weight: 700;
}

.regular {
  font-weight: 400 !important;
}

.medium {
  font-weight: 600;
}

.thin {
  font-weight: 300;
}

.light {
  font-weight: 100;
}

/* letter spacing */

.text-decoration-none {
  text-decoration: none;
}

/*error and success messages*/

.error-bg {
  background: #f59c9c;
  color: #ba0505;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  letter-spacing: 0.02em;
}
.error {
