/*404 */
.not-found {
  padding: 20vh 0;
}

.not-found form {
  width: 100% !important;
  max-width: 500px;
  display: block !important;
  margin: 20px auto;
}

// loading icon
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 25px;
  height: 25px;
  margin: 8px;
  border: 3px solid var(--light-secondary);
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: var(--light-secondary) transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

// notification
#add-to-cart-notification {
  color: var(--green);
}

// pulse loading
.dots-loading {
  width: 56px;
  padding: 8px 0;
  background:
    radial-gradient(circle closest-side, var(--light-on-primary) 90%, #0000) 0%
      50%,
    radial-gradient(circle closest-side, var(--light-on-primary) 90%, #0000) 50%
      50%,
    radial-gradient(circle closest-side, var(--light-on-primary) 90%, #0000)
      100% 50%;
  background-size: calc(100% / 3) 8px;
  background-repeat: no-repeat;
  animation: dots-7ar3yq 1s infinite linear;
  margin: 0 auto;
}

@keyframes dots-7ar3yq {
  20% {
    background-position:
      0% 0%,
      50% 50%,
      100% 50%;
  }

  40% {
    background-position:
      0% 100%,
      50% 0%,
      100% 50%;
  }

  60% {
    background-position:
      0% 50%,
      50% 100%,
      100% 0%;
  }

  80% {
    background-position:
      0% 50%,
      50% 50%,
      100% 100%;
  }
}
// slick style
.slick-slider {
  margin: 0 auto;
  .slick-next {
    right: 0;
  }
  .slick-prev {
    left: -20px;
  }
  .slick-arrow {
    z-index: 2;

    &::before {
      color: var(--primary);
      font-size: 40px;
    }
  }

  .slick-dots {
    position: static;
  }
}

.success-message {
  background-color: var(--dark-tertiary-container);
  padding: 8px 8px 8px 8px;
  margin-top: 8px;
  display: none;
  margin-bottom: 8px;
  border-radius: 4px;
  width: 100%;
  color: var(--dark-on-tertiary-container);

  p {
    color: var(--dark-on-tertiary-container);
    margin-left: 4px;
    display: inline-block;
  }
  svg {
    position: relative;
    top: 4px;
    path {
      fill: var(--dark-on-tertiary-container);
    }
  }
  a {
    color: var(--dark-on-tertiary-container) !important;

    text-decoration: underline !important;
  }
}

.error-message {
  display: none;
  border-radius: 4px;
  width: 100%;
  background-color: var(--dark-error-container);
  padding: 16px;
  margin-top: 8px;
  margin-bottom: 8px;
  color: var(--dark-on-error);

  p {
    color: var(--dark-on-error);
    margin-left: 4px;
    display: inline-block;
  }
  svg {
    position: relative;
    top: 4px;
    path {
      fill: var(--error);
    }
  }
}

// slick nav buttons
.slick-slider {
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 1000 !important;
    svg {
      box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
      border-radius: 50%;
    }
  }
  .next-arrow {
    right: -18px;
  }
  .prev-arrow {
    left: -18px;
  }
  .slick-dots {
    margin-top: 8px !important;
    li {
      width: 28px;
      height: 28px;
    }
    button {
      width: 28px;
      height: 28px;
      &::before {
        line-height: 28px;
        font-size: 18px;
        width: 28px;
        height: 28px;
      }
    }
  }
}

.skeleton {
  background: gray;
  height: 100%;
  width: 100%;

  position: absolute;
  z-index: 5000;
}

// header file
.skeleton {
  animation: skeleton-loading 1s linear infinite alternate;
}

@keyframes skeleton-loading {
  0% {
    background-color: hsl(200, 20%, 80%);
  }
  100% {
    background-color: hsl(200, 20%, 95%);
  }
}

.skeleton-loading {
  width: 100%;
  position: absolute;
  height: 100%; /* adjust height to match your image aspect ratio */
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 10px;
  display: inline-block;
}

.skeleton-loading img {
  opacity: 0;
  transition: opacity 0.5s;
}

.skeleton-loading img.loaded {
  opacity: 1;
}
// enquire button
.enquire-now-button {
  width: 100%;
}

// divider
.divider {
  border-top: 1px solid var(--light-outline-variant);
}

// index page styling
.page-template-default {
  .index-page {
    min-height: 60vh;
    background-color: var(--light-surface-container-low);
    .title-wrapper {
      background-color: var(--light-surface-container-low);
      border-bottom: 1px solid var(--light-outline-variant);
      padding-bottom: 40px;
      padding-top: 40px;
      @media (max-width: 600px) {
        padding-top: 24px;
        padding-bottom: 24px;
      }
      #breadcrumbs {
        text-align: center;
      }
    }
    .content {
      h2 {
        font-size: 2rem;
        margin-top: 24px;
        color: var(--light-on-surface);
      }
      h3 {
        font-size: 1.5rem;
        margin-top: 24px;
        color: var(--light-on-surface);
      }
      p,
      li {
        font-size: 1rem;
        color: var(--light-on-surface-variant);
        line-height: 1.5rem;
        margin: 8px 0;
      }
      ul {
        list-style: disc;
        margin: 0 0 0 24px;
      }

      .wp-block-button {
        margin-top: 40px;
      }
      .has-text-align-center {
        text-align: center;
      }
      margin-top: 24px;
      // row layout styling
      .row-layout {
        padding-top: 24px;
        padding-bottom: 24px;
      }
      .row-layout:nth-child(odd) {
        background-color: var(--light-surface-container-high);
      }
      .row-layout:nth-child(even) {
        background-color: var(--light-surface-container);
      }
      hr {
        margin: 24px 0;
      }
      // block columns styling
      .wp-block-columns {
        display: grid;
        grid-template-columns: auto 400px;
        gap: 40px;
        justify-content: center;
        align-items: start;
        p {
          margin: 8px 0;
          a {
            text-decoration: none;

            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      // used for forms
      .wp-block-group {
        background-color: var(--light-surface-container-lowest);
        max-width: 800px;
        padding: 24px;
        margin: 0 auto;
        border-radius: 12px;
        border: 1px solid var(--light-outline-variant);
        h2 {
          font-size: 1.5rem;
          margin: 16px 0;
        }
      }
    }
  }
}

// img styleing
.img-fill {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 100;
}
